import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';

/**
 * Generated class for the EscanerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-escaner',
  templateUrl: 'escaner.html',
})
export class EscanerPage {





  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public loadingController:LoadingController) {



  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EscanerPage');
  }





  goVideo(link){
	
		   this.apiProvider.getYoutubeDataVideo(link)
      .then(data => {
       console.log(data.items[0]);
          this.navCtrl.push('YoutubevidePage',data.items[0]);
      });


}


  escanearCodigo() {

  	  var me = this;

    let loading = me.loadingController.create({content : "cargando..."});
    loading.present();
 
    console.log('escanearCodigo');

       cordova.plugins.barcodeScanner.scan(
      function (result) {
      console.log(result);
      //var ref = cordova.InAppBrowser.open(result.text, '_system', 'location=yes');

       me.apiProvider.getYoutubeLink(result.text)
      .then(data => {
       loading.dismissAll();
       if(data){
       console.log(data);
       //console.log(data.url);
	  // var youtubeVideo = data.url.split("v=")[1];
	  var youtubeVideo = data.split('href="https://www.youtube.com/watch?v=')[1].split('">')[0];
	


	   //YoutubeVideoPlayer.openVideo(youtubeVideo, function(result) { console.log('YoutubeVideoPlayer result = ' + result); console.log(result);});

	   me.goVideo(youtubeVideo);

       }

       else{
       console.log('errQR');
       console.log(data);
       }



      });


      },
      function (error) {
          alert("Ups, ha ocurrido un error: " + error);
      },
      {

          preferFrontCamera : true, // iOS and Android
          showFlipCameraButton : true, // iOS and Android
          showTorchButton : true, // iOS and Android
          torchOn: true, // Android, launch with the torch switched on (if available)
          saveHistory: true, // Android, save scan history (default false)
          prompt : "Coloca el QR de la maquina dentro del recuadro", // Android
          resultDisplayDuration: 500, // Android, display scanned text for X ms. 0 suppresses it entirely, default 1500
          formats : "QR_CODE,PDF_417", // default: all but PDF_417 and RSS_EXPANDED
          orientation : "landscape", // Android only (portrait|landscape), default unset
          disableAnimations : true, // iOS
          disableSuccessBeep: false // iOS and Android
      }
   );


  }





}



